
.container {
    height: 10vh;
}
  
.main-logo {
    display: flex;
    flex: 20%;
}
  
header {
    width: 100%;
    height: 64px;
    text-align: center;
    background: #282c34;
}
  
.Img-logo {
    max-height: 100%;
    height: 100%;
}
  
.Navbar {
    display: flex;
    justify-content: center;
    background: #282c34;
    align-items: center;
}
  
.Nav-list {
    display: flex;
    padding-left: 0;
    list-style-type: none;
    gap: 2vw;
    justify-content: center;
    font-size: larger;
}
  
.Navbar-collapse {
    flex: 60%;
}
  
.jordan, .nike, .adidas {
    margin: 0;
}
  
a {
    text-decoration: none;
    color: white;
}
  
.log {
    background: rgb(165, 33, 33);
}

